const props = defineProps({
  show: {
    type: Boolean,
    required: true
  },
  isLightTheme: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['close', 'select-agent']);

// Watch show prop for debugging
watch(() => props.show, (newValue) => {
  console.log('AiAgentsList show prop changed:', newValue);
});

// Dummy data for demonstration
const agents = ref([
  {
    id: 1,
    name: 'Trading helper',
    agentTag: 'trading_helper',
    avatar: new URL('@/assets/agents/bitcat-ai-hero.png', import.meta.url).href,
    creatorPower: '19,135',
