    avatar: new URL('@/assets/agents/bitcat-ai-hero.png', import.meta.url).href,
    creatorPower: '1,972',
    creator: 'BITCAT'
  }
]);

const selectAgent = (agent) => {
  emit('select-agent', agent);
  emit('close');
};
