<template>
  <div class="multiplayer-container">
    <multiplayer-header 
      :player-stats="playerStats"
      :opponent-stats="opponentStats"
      @show-stats-info="showStatsInfo = true"
      @generate-opponent="generateNewOpponent"
      @start-game="startGame"
    />
    
    <div class="game-status">
      <div class="status-title">Game Status</div>
      <div class="score-panel">
        <div class="score-item">Score: <span>{{ score }}</span>₿</div>
        <div class="score-item">Lives: <span>{{ lives }}</span></div>
        <div class="score-item">Time: <span>{{ timeLeft }}</span>s</div>
        <div class="score-item">Strength: <span>{{ playerStats.strength }}</span></div>
        <div class="score-item">Agility: <span>{{ playerStats.agility }}</span></div>
        <div class="score-item">Stamina: <span>{{ playerStats.stamina }}</span></div>
        <div class="score-item">Luck: <span>{{ playerStats.luck }}</span></div>
        <div class="score-item">Intelligence: <span>{{ playerStats.intelligence }}</span></div>
      </div>
    </div>
    
    <multiplayer-game-board 
      ref="gameBoard"
      @cat-hit="onCatHit"
    />
    
    <multiplayer-controls 
      :score="score"
      :lives="lives"
      :time-left="timeLeft"
      @restart-game="startGame"
    />
    
    <multiplayer-score-board 
      :online-players="onlinePlayers"
      :players="players"
      :achievements="achievements"
      @challenge-player="challengePlayer"
    />
  </div>
</template>

<script>
import MultiplayerHeader from '@/components/multiplayer/MultiplayerHeader.vue'
import MultiplayerGameBoard from '@/components/multiplayer/MultiplayerGameBoard.vue'
import MultiplayerControls from '@/components/multiplayer/MultiplayerControls.vue'
import MultiplayerScoreBoard from '@/components/multiplayer/MultiplayerScoreBoard.vue'

export default {
  name: 'MultiplayerGamePage',
  components: {
    MultiplayerHeader,
    MultiplayerGameBoard,
    MultiplayerControls,
    MultiplayerScoreBoard
  },
  data() {
    return {
      showStatsInfo: false,
      playerStats: {
        strength: 5,
        agility: 5,
        stamina: 5,
        luck: 5,
        intelligence: 5
      },
      opponentStats: {
        strength: 5,
        agility: 5,
        stamina: 5,
        luck: 5,
        intelligence: 5
      },
      score: 0,
      lives: 3,
      timeLeft: 10,
      gameTimer: null,
      onlinePlayers: 42,
      players: [
        { id: 1, name: 'Player 1', rank: 'Gold', avatar: '@/assets/avatar1.png' },
        { id: 2, name: 'Player 2', rank: 'Silver', avatar: '@/assets/avatar2.png' },
        { id: 3, name: 'Player 3', rank: 'Bronze', avatar: '@/assets/avatar3.png' }
      ],
      achievements: [
        { 
          id: 1, 
          name: 'First Win', 
          description: 'Win your first game', 
          achieved: true,
          icon: '@/assets/achievement1.png'
        },
        { 
          id: 2, 
          name: 'Score Master', 
          description: 'Score 1000 points', 
          achieved: false,
          icon: '@/assets/achievement2.png'
        }
      ]
    }
  },
  methods: {
    generateNewOpponent() {
      this.opponentStats = {
        strength: Math.floor(Math.random() * 10) + 1,
        agility: Math.floor(Math.random() * 10) + 1,
        stamina: Math.floor(Math.random() * 10) + 1,
        luck: Math.floor(Math.random() * 10) + 1,
        intelligence: Math.floor(Math.random() * 10) + 1
      }
    },
    startGame() {
      if (this.gameTimer) {
        clearInterval(this.gameTimer)
      }
      this.score = 0
      this.timeLeft = 10
      this.lives = 3
      this.$refs.gameBoard.startGame()
      this.gameTimer = setInterval(this.updateTimer, 1000)
    },
    updateTimer() {
      if (this.timeLeft > 0) {
        this.timeLeft--
      } else {
        this.stopGame()
      }
    },
    stopGame() {
      clearInterval(this.gameTimer)
      this.$refs.gameBoard.stopGame()
    },
    onCatHit() {
      this.score += 10
    },
    challengePlayer(playerId) {
      console.log('Challenging player:', playerId)
      // Implement challenge logic here
    }
  },
  beforeUnmount() {
    if (this.gameTimer) {
      clearInterval(this.gameTimer)
    }
  }
}
</script>

