    // works good for dao
    const mintAddress = "4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump";

    async function getTokenHolders() {
        try {
            // Получение всех токен-аккаунтов по программе SPL Token
            const accounts = await connection.getProgramAccounts(new PublicKey("TokenkegQfeZyiNwAJbNbGKPFXCWuBvf9Ss623VQ5DA"), {
                filters: [
                    {
                        dataSize: 165, // Размер токен-аккаунта
                    },
                    {
                        memcmp: {
                            offset: 0, // Смещение для адреса mint
                            bytes: mintAddress,
                        },
                    },
                ],
            });

            if (accounts.length === 0) {
                console.log("Нет владельцев токена или токен не существует.");
                return [];
            }

            console.log("Владельцы токена:", accounts);


            const holders = accounts.reduce((acc, { pubkey, account }) => {
                const data = account.data;

                // Владелец токен-аккаунта
                const owner = new PublicKey(data.slice(32, 64)).toBase58();

               const amountBuffer = data.slice(64, 72);
               const rawAmount = Number(amountBuffer.readBigUInt64LE());
               const amount = Math.floor(rawAmount / Math.pow(10, 6)).toFixed(0); // получаем целое число токеновunt / Math.pow(10, 9); // делим на 10^9 для получения реального количества токенов
                
                if (amount < 1) {
                    return acc;
                }

                const wallet = pubkey.toBase58();

                acc[wallet] = {
                    owner,
                    amount,
                };

                return acc;
            }, {});

            return holders;
        } catch (error) {
            console.error("Error to get token holders:", error);
        }
    }

    // Вызов функции
    getTokenHolders().then((holders) => {
        console.log("Список держателей токена:", holders);
    });

