<script>
import axios from 'axios';
import { mapState, mapActions } from 'vuex';

export default {
  name: 'PlayerPage',
  data() {
    return {
      playerData: {
        userTag: '',
        walletAddress: '',
        bestScore: 0,
        totalScore: 0,
        wins: 0,
        losses: 0,
        stats: {
          strength: 2,
          agility: 2,
          stamina: 2,
          luck: 2,
          intelligence: 2
        }
      },
      gameHistory: []
    };
  },
  async created() {
    const userTag = this.$route.params.userTag;
    await this.fetchPlayerData(userTag);
    await this.fetchGameHistory(userTag);
  },
  computed: {
    ...mapState({
      hideSeekStats: state => state.hideSeek.stats
    }),
    calculateWinRate() {
      if(!this.hideSeekStats?.wins) {
        return 'NaN'
      }
      
      const total = this.hideSeekStats?.wins + this.hideSeekStats?.losses;
      if (total === 0) return 0;
      return Math.round((this.hideSeekStats?.wins / total) * 100);
    }
  },
  methods: {
    ...mapActions(['fetchHideSeekPlayerStats']),
    async fetchPlayerData(userTag) {
      try {
        const [userData, hideSeekStats] = await Promise.all([
          axios.get(`/user/${userTag}`),
          this.fetchHideSeekPlayerStats(userTag)
        ]);
        this.playerData = {
          ...userData.data,
          hideSeekStats: hideSeekStats
        };
      } catch (error) {
        console.error('Error fetching player data:', error);
      }
    },
    async fetchGameHistory(userTag) {
      try {
        const response = await axios.get(`/user/${userTag}/history`);
        this.gameHistory = response.data.gameHistory.slice(0, 50); // Limit to 50 games
      } catch (error) {
        console.error('Error fetching game history:', error);
      }
    },
    formatDate(timestamp) {
      const date = new Date(timestamp);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    formatWallet(address) {
      if (!address) return 'not linked';
      return `${address.slice(0, 6)}...${address.slice(-4)}`;
    },
    challengeToBattle() {
      // alert('Challenge sent! Get ready for battle! ⚔️');
    }
  }
};
</script>

